/* purgecss start ignore */

.switch {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 20px;
  margin-bottom:10px;
}

/* Hide default HTML checkbox */

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background-color: #fff;
  transition: .4s;
}

input:checked + .slider {
  background-color: #171717;
}

input:focus + .slider {
  box-shadow: 0 0 1px 171717;
}

input:checked + .slider:before {
  -webkit-transform: translateX(10px);
  transform: translateX(10px);
}

/* Rounded sliders */

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

